/* Full screen background */
/* Thin, clean divider */

.format-page {
  height: 100vh;
  width: 100%;

  font-family: "Segoe UI", sans-serif;
  margin: 0;
  padding: 0;
}

/* Full screen white workspace */
.format-card {
  height: 100vh;
  width: 100%;
  background: #ffffff;
  border-radius: 0;          /* remove popup look */
  box-shadow: none;          /* remove modal shadow */
  padding: 80px 120px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

/* Headings */
.format-card h2 {
  font-size: 32px;
  color: #1a73e8;
  margin-bottom: 6px;
}

.format-card p {
  color: #475569;
  font-size: 16px;
  margin-bottom: 50px;
}

/* Options row */
.format-options {
  display: flex;
  gap: 80px;
  justify-content: center;
}

/* Format boxes */
.format-box {
  width: 280px;
  height: 230px;
  border-radius: 20px;
  background: #f1f5f9;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 18px 40px rgba(0, 0, 0, 0.15);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 46px;
}

.format-box:hover {
  transform: translateY(-12px) scale(1.08);
  background: #1a73e8;
  color: #ffffff;
}

/* Text */
.format-box h4 {
  margin-top: 12px;
  font-size: 18px;
  font-weight: 600;
}

.format-box small {
  font-size: 13px;
  opacity: 0.85;
}

/* Responsive */
@media (max-width: 900px) {
  .format-options {
    flex-direction: column;
    gap: 40px;
  }

  .format-card {
    padding: 50px 30px;
  }

  .format-box {
    width: 100%;
    height: auto;
    padding: 40px 20px;
  }
}
/* ===== HEADER ===== */

.logo {
  height: 38px;
  object-fit: contain;
}

/* ===== BACK BUTTON ===== */
.back-wrapper {
  padding: 0 28px;          /* ‚ùå no vertical padding */
  margin-top: 6px;          /* ‚úÖ small spacing without band */
}
.back-btn {
  background: #fff;
  border: 1px solid #d1d5db;
  padding: 6px 14px;
  border-radius: 6px;
  cursor: pointer;
  font-size: 14px;
}

.back-btn:hover {
  background: #f3f4f6;
}.back-wrapper {
  background: transparent;
}