/* ================= GLOBAL RESET ================= */

html,
body,
#root {
  height: 100%;
}
.dashboard {
  display: flex;
  flex-direction: column;
  align-items: stretch;
}
.filters-with-counter {
  position: relative;
  z-index: 1;
}

.dashboard-header {
  order: -1;                /* üö® FORCES HEADER TO TOP */
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;

  padding: 14px 24px;
  background: #ffffff;
  border-radius: 14px;
  margin-bottom: 18px;

  box-shadow: 0 6px 20px rgba(0,0,0,0.08);

  position: relative;
  z-index: 9999;
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif;
  background: #f4f6fb;
  color: #222;
}

/* ================= DASHBOARD LAYOUT ================= */

.dashboard {
  max-width: 1400px;
  margin: 0 auto;
  padding: 16px;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  position: relative;
}

/* ================= HEADER (FIXED & VISIBLE) ================= */

.dashboard-header {
  display: flex;
  align-items: center;
  justify-content: space-between;

  padding: 14px 24px;
  background: #ffffff;
  border-radius: 14px;

  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.08);
  margin-bottom: 18px;

  position: relative;       /* üî• CRITICAL */
  z-index: 2000;             /* üî• CRITICAL */
}

.dashboard-title {
  font-size: 28px;
  font-weight: 600;
  letter-spacing: 0.06em;
  text-align: center;
  flex: 1;
}

/* Logos */

.logo {
  height: 36px;
  width: auto;
  object-fit: contain;
}

.left-logo {
  margin-right: 12px;
}

.right-logo {
  margin-left: 8px;
}

/* Header right section */

.header-right {
  display: flex;
  align-items: center;
  gap: 12px;
}

/* ================= SIGN IN / OUT BUTTON ================= */

.sign-in-btn,
.sign-out-btn {
  padding: 6px 14px;
  font-size: 14px;
  background-color: #1976d2;
  color: #ffffff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  white-space: nowrap;
}

.sign-in-btn:hover,
.sign-out-btn:hover {
  background-color: #155fa0;
}

/* ================= FILTERS + COUNTERS ================= */

.filters-with-counter {
  display: flex;
  align-items: stretch;
  gap: 16px;
  margin-bottom: 16px;

  position: relative; /* üî• prevents overlap */
  z-index: 1;
}

/* Filters panel */

.filters-panel {
  flex: 1;
  background: #ffffff;
  border-radius: 12px;
  padding: 8px 12px;
  box-shadow: 0 6px 16px rgba(15, 23, 42, 0.08);
}

.filter-row {
  display: flex;
  gap: 12px;
  margin-bottom: 6px;
}

.filter-row:last-child {
  margin-bottom: 0;
}

.filter-select {
  min-width: 180px;
  height: 32px;
  padding: 4px 8px;
  border-radius: 8px;
  border: 1px solid #d0d7e2;
  background: #f9fafb;
  font-size: 12.5px;
}

.filter-select:focus {
  border-color: #2563eb;
  background: #ffffff;
  box-shadow: 0 0 0 2px rgba(37, 99, 235, 0.15);
  outline: none;
}

/* ================= SURVEY COUNTERS ================= */

.survey-counter-column {
  display: flex;
  gap: 10px;
  min-width: 230px;
}

.survey-counter-mini {
  flex: 1;
  color: #ffffff;
  padding: 8px 10px;
  border-radius: 14px;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  box-shadow: 0 8px 20px rgba(15, 23, 42, 0.25);
}

.survey-counter-total {
  background: linear-gradient(135deg, #2563eb, #22c55e);
}

.survey-counter-filtered {
  background: linear-gradient(135deg, #7c3aed, #ec4899);
}

.survey-counter-small-label {
  font-size: 11px;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.survey-counter-small-value {
  font-size: 24px;
  font-weight: 700;
}

.survey-counter-small-unit {
  font-size: 12px;
}

/* ================= CONTENT ================= */

.current-filters {
  margin-bottom: 14px;
  font-size: 13px;
  color: #4b5563;
}

/* ================= CHART GRID ================= */

.charts-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 18px;
}

.chart-card {
  background: #ffffff;
  border-radius: 12px;
  padding: 10px 12px 6px;
  box-shadow: 0 6px 16px rgba(15, 23, 42, 0.08);
}

.chart-card-title {
  font-size: 14px;
  font-weight: 600;
  text-align: center;
  margin-bottom: 4px;
}

/* ================= RESPONSIVE ================= */

@media (max-width: 1200px) {
  .charts-grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (max-width: 900px) {
  .charts-grid {
    grid-template-columns: repeat(2, 1fr);
  }

  .filters-with-counter {
    flex-direction: column;
  }
}

@media (max-width: 600px) {
  .charts-grid {
    grid-template-columns: 1fr;
  }

  .filter-row {
    flex-wrap: wrap;
  }
}