/* Full screen page */

/* ‚ùå REMOVE ANY INHERITED BLUE / DARK HEADER */
.unit-page {
  background: #ffffff;
}

/* Force remove header background */
.simple-header {
  background: #ffffff !important;
  box-shadow: none !important;
  border-bottom: 1px solid #e5e7eb;
}

/* Kill any leftover dashboard header styles */
.dashboard-header,
.header-left,
.header-right {
   /* display: none */
}


/* ===== UNIT PAGE WRAPPER ===== */
.unit-page {
  min-height: 100vh;
  background: #f6f8fc;
}

/* ===== CENTER CARD ===== */
.unit-card {
  max-width: 900px;
  margin: 60px auto;
  background: #ffffff;
  border-radius: 18px;
  padding: 48px 32px;
  box-shadow: 0 18px 40px rgba(0, 0, 0, 0.08);
  text-align: center;
}

/* ===== TITLE ===== */
.unit-title {
  font-size: 32px;
  font-weight: 700;
  color: #0f172a;
}

.unit-title span {
  color: #2563eb;
}

.unit-subtitle {
  margin-top: 6px;
  font-size: 15px;
  color: #475569;
}

/* ===== ZONE SELECT (FIXED AS REQUESTED) ===== */
.zone-card {
  margin-top: 24px;              /* slightly higher */
  margin-bottom: 36px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.zone-card label {
  font-size: 15px;
  font-weight: 700;              /* ‚úÖ bold */
  color: #1f2937;
  margin-bottom: 8px;
  letter-spacing: 0.4px;
}

.zone-card select {
  min-width: 220px;
  padding: 10px 14px;
  border-radius: 10px;
  border: 1px solid #cbd5e1;
  font-size: 14px;
  background: #ffffff;
  cursor: pointer;
  outline: none;
}

.zone-card select:focus {
  border-color: #2563eb;
  box-shadow: 0 0 0 2px rgba(37, 99, 235, 0.15);
}

/* ===== UNIT OPTIONS ===== */
.unit-options {
  display: flex;
  gap: 32px;
  justify-content: center;
  margin-top: 10px;
}

.unit-box {
  width: 260px;
  padding: 28px 20px;
  background: #f1f5f9;
  border-radius: 18px;
  cursor: pointer;
  transition: all 0.25s ease;
  box-shadow: 0 12px 30px rgba(0, 0, 0, 0.08);
}

.unit-box:hover {
  transform: translateY(-6px);
  box-shadow: 0 18px 40px rgba(0, 0, 0, 0.14);
}

.unit-box h3 {
  margin-top: 12px;
  font-size: 18px;
  font-weight: 600;
  color: #0f172a;
}

.unit-box small {
  color: #475569;
}