.login-root {
    position: relative;
    height: 100vh;
    overflow: hidden;
    background: #0e1624;
  }
  
  .login-bg {
    position: absolute;
    inset: 0;
    background-image: url("../assets/rail-bg.jpg");
    background-size: cover;
    background-position: center;
    filter: saturate(0.9) contrast(1.05);
    transform: scale(1.08);
  }
  
  .login-overlay {
    position: absolute;
    inset: 0;
    background: linear-gradient(
      rgba(8, 12, 25, 0.75),
      rgba(8, 12, 25, 0.85)
    );
    backdrop-filter: blur(3px);
  }
  
  .login-content {
    position: relative;
    z-index: 2;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .login-box {
    width: 340px;
    padding: 32px;
    background: rgba(255, 255, 255, 0.12);
    backdrop-filter: blur(12px);
    border-radius: 12px;
    box-shadow: 0 15px 40px rgba(0, 0, 0, 0.4);
  }
  
  .login-box h2 {
    color: #fff;
    text-align: center;
    margin-bottom: 20px;
  }
  
  .login-box input {
    width: 100%;
    padding: 12px;
    margin-bottom: 14px;
    border: none;
    border-radius: 6px;
  }
  
  .login-box button {
    width: 100%;
    padding: 12px;
    background: #1976d2;
    color: white;
    border: none;
    border-radius: 6px;
    font-weight: bold;
    cursor: pointer;
  }
  .simple-header {
    position: relative;
    height: 70px;
    padding: 10px 20px;
  }
  
  /* IRCTC LOGO ‚Äì TOP LEFT */
  .left-logo {
    position: absolute;
    top: 10px;
    left: 20px;
    height: 40px;
  }
  
  /* DATAWISE LOGO ‚Äì TOP RIGHT */
  .right-logo {
    position: absolute;
    top: 10px;
    right: 20px;
    height: 40px;
  }
  
  /* BACK BUTTON ‚Äì BELOW IRCTC LOGO */
  .back-btn {
    position: absolute;
    top: 80px;        /* üëà push it below the line */
    left: 20px;
    padding: 4px 10px;
    font-size: 13px;
    border: 1px solid #ccc;
    background: #fff;
    cursor: pointer;
    border-radius: 4px;
  }
  .back-btn:hover {
    background: #f0f0f0;
  }