/* Page background */
.pdf-page {
  width: 100%;
  min-height: 100vh;
  background: #f3f4f6;
}
/* Main card */
.pdf-card {
  width: 100%;
  min-height: 100vh;
  background: #ffffff;
  border-radius: 0;
  box-shadow: none;
  padding: 32px 64px;
}

/* Header */
.pdf-card h2 {
  font-size: 26px;
  font-weight: 700;
  margin-bottom: 4px;
  color: #1f2937;
}

.pdf-card p {
  color: #6b7280;
  font-size: 14px;
  margin-bottom: 20px;
}

/* Sign out */
.signout-btn {
  float: right;
  background: #ef4444;
  color: white;
  border: none;
  padding: 8px 14px;
  border-radius: 8px;
  font-size: 13px;
  cursor: pointer;
}

.signout-btn:hover {
  background: #dc2626;
}

/* Month section */
.month-section {
  background: #f9fafb;
  border: 1px solid #e5e7eb;
  border-radius: 12px;
  padding: 18px 20px;
  margin-bottom: 22px;
}

/* Month title */
.month-section h3 {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 14px;
  color: #111827;
}

/* No PDF text */
.no-pdf {
  color: #9ca3af;
  font-style: italic;
  padding: 10px 0;
}

/* PDF list grid */
.pdf-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(260px, 1fr));
  gap: 14px;
}

/* PDF item */
.pdf-item {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 12px 14px;
  border-radius: 10px;
  border: 1px solid #d1d5db;
  background: #ffffff;
  cursor: pointer;
  transition: all 0.2s ease;
  font-size: 14px;
}

.pdf-item:hover {
  border-color: #2563eb;
  background: #eff6ff;
}

.pdf-item input {
  transform: scale(1.1);
  accent-color: #2563eb;
}

.pdf-item span {
  color: #1f2937;
  line-height: 1.3;
}

/* Download button */
.download-btn {
  margin-top: 26px;
  width: 100%;
  padding: 14px;
  border-radius: 10px;
  border: none;
  background: #2563eb;
  color: white;
  font-size: 15px;
  font-weight: 600;
  cursor: pointer;
}

.download-btn:disabled {
  background: #9ca3af;
  cursor: not-allowed;
}

.download-btn:not(:disabled):hover {
  background: #1d4ed8;
}
/* ===== HEADER ===== */
.simple-header {
  display: flex;
  justify-content: space-between; /* üî• THIS pushes corners */
  align-items: center;
  width: 100%;
  padding: 12px 0;
  border-bottom: 1px solid #e5e7eb;
}

.logo {
  height: 38px;
  object-fit: contain;
}

/* optional but makes intent clear */
.left-logo {
  margin-left: 0;
}

.right-logo {
  margin-right: 0;
}

/* ===== BACK BUTTON ===== */
.back-wrapper {
  margin-top: 10px;
}

.back-btn {
  background: #fff;
  border: 1px solid #d1d5db;
  padding: 6px 14px;
  border-radius: 6px;
  cursor: pointer;
  font-size: 14px;
}

.back-btn:hover {
  background: #f3f4f6;
}
