.month-page {
  height: 100vh;
  background: #ffffff;
  font-family: "Segoe UI", sans-serif;
}

.month-wrapper {
  height: 100%;
  padding: 80px;
  text-align: center;
}

.month-wrapper h2 {
  font-size: 32px;
  color: #1a73e8;
}

.month-wrapper p {
  margin: 10px 0 50px;
  color: #475569;
}

/* Slider container */
.slider-container {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 30px;
}

/* Arrow buttons */
.arrow {
  font-size: 26px;
  padding: 10px 16px;
  border-radius: 10px;
  border: none;
  cursor: pointer;
  background: #e8f0fe;
}

.arrow:disabled {
  opacity: 0.4;
  cursor: not-allowed;
}

/* Window */
.slider-window {
  width: 320px;
  overflow: hidden;
}

/* Track */
.slider-track {
  display: flex;
  transition: transform 0.4s ease;
}

/* Slide */
.month-slide {
  min-width: 320px;
  height: 160px;
  background: #f1f5f9;
  border-radius: 18px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 22px;
  font-weight: 600;
  cursor: pointer;
  box-shadow: 0 20px 40px rgba(0,0,0,0.15);
}

/* Selected */
.month-slide.active {
  background: #1a73e8;
  color: #ffffff;
}

/* Continue */
.continue-btn {
  margin-top: 60px;
  padding: 14px 48px;
  font-size: 16px;
  border-radius: 10px;
  border: none;
  background: #1a73e8;
  color: white;
  cursor: pointer;
}
/* ===== HEADER ===== */
.simple-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 24px;
}

.logo {
  height: 42px;
}

.header-divider {
  border: none;
  border-top: 1px solid #e5e7eb;
  margin: 0 0 10px 0;
}

/* ===== BACK BUTTON ===== */
.back-btn {
  margin-left: 24px;
  margin-bottom: 20px;
  padding: 6px 14px;
  font-size: 14px;
  border: 1px solid #d1d5db;
  background: #ffffff;
  border-radius: 6px;
  cursor: pointer;
}

.back-btn:hover {
  background: #f3f4f6;
}